import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/styles";
import ProfileCard from "./../components/ProfileCard";

import { TweenMax, TimelineLite } from "gsap";
import IntroCard from "../components/IntroCard";
import WhatIDoCard from "../components/WhatIDoCard";
import { Button } from "@material-ui/core";
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import { Link } from "react-router-dom";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const useStyles = makeStyles((theme) => ({
  aboutWrapper: {
    display: "grid",
    gridTemplateColumns: "7fr 15fr",
    gridTemplateRows: "auto 1fr",
    // gridTemplateColumns: "repeat(3,200px)", //example on repeat, that do three columns with the same size
    // gridAutoRows:"15px" // exmple on repeating rows
    gridColumnGap: "10px",
    gridTemplateAreas: `'card main'`,
  },
  // aboutWrapperSM: {
  //   display: "grid",
  //   gridTemplateColumns: "1fr auto",
  //   gridTemplateRows: "auto 1fr",
  //   // gridTemplateColumns: "repeat(3,200px)", //example on repeat, that do three columns with the same size
  //   // gridAutoRows:"15px" // exmple on repeating rows
  //   gridColumnGap: "10px",
  //   gridTemplateAreas: `'card main'`,
  // },
  profileCardDiv: {
    gridArea: "card",
  },
  mainDiv: {
    // JUST AS A REFRERENCE ANOTHER WAY TO DO IT
    // gridColumnStart: 3,
    // gridRowStart: 2,
    // gridColumnEnd: 4
    gridArea: "main",
  },
  downloadButton: {
    marginTop: "10px",
  },
}));

const ResumeAbout = () => {
  // const smMatches = useMediaQuery((theme) => theme.breakpoints.up("sm"));
  const classes = useStyles();
  useEffect(() => {
    var timeline = new TimelineLite();
    timeline.from("#profileCardDiv", 0.8, {
      x: -500,
      ease: "circ.out",
    });
    timeline.from("#WhatIDoCard", 0.5, {
      y: -1000,
      ease: "circ.out",
    });
    timeline.from("#IntroCard", 0.5, {
      y: -1000,
      ease: "circ.out",
    });
  });

  return (
    <div className={classes.aboutWrapper}>
      <div className={classes.profileCardDiv} id="profileCardDiv">
        <ProfileCard />
        <Button
          fullWidth
          component={Link}
          to="/files/fawzy.pdf"
          target="_blank"
          variant="contained"
          color="secondary"
          size="large"
          elevation={0}
          className={classes.downloadButton}
          startIcon={<CloudDownloadIcon />}
        >
          Download resume in PDF
        </Button>
      </div>
      <div className={classes.mainDiv} id="mainDiv">
        <div id="IntroCard">
          <IntroCard />
        </div>
        <div id="WhatIDoCard">
          <WhatIDoCard />
        </div>
      </div>
    </div>
  );
};

export default ResumeAbout;
