import React from "react";
import { makeStyles } from "@material-ui/styles";
import { Card, CardHeader, CardContent } from "@material-ui/core";
import { Typography } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { ListItemText } from "@material-ui/core";
import { ListItemAvatar } from "@material-ui/core";
import { ListItem } from "@material-ui/core";
import { Divider } from "@material-ui/core";
import { List } from "@material-ui/core";
import CodeIcon from "@material-ui/icons/Code";
import CloudIcon from "@material-ui/icons/Cloud";
import DnsIcon from "@material-ui/icons/Dns";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import PaymentIcon from "@material-ui/icons/Payment";
import SettingsInputCompositeIcon from "@material-ui/icons/SettingsInputComposite";

import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
  card: {
    maxWidth: "100%",
    marginBottom: "10px",
    // backgroundImage: `url(${cardTexture})`,
    // backgroundSize: "cover"
  },
  headerRoot: {
    backgroundColor: "rgba(0, 0, 0, 0.8)",
  },

  cardContentRoot: {
    backgroundColor: "rgba(0, 0, 0, 0.8)",
  },
  headerTitle: {
    color: "rgba(243, 167, 18, 1)",
    fontWeight: 900,
    fontSize: "1.2rem",
  },
  headerSubHeader: {
    fontWeight: 900,
    color: "#262626",
    fontSize: "0.9rem",
  },
  inline: {
    display: "inline",
  },
  avatar: {
    backgroundColor: "#e4e4e2",
  },

  textSecondary: { color: "#e4e4e2" }, //white gray
  textPrimary: { color: "#262626" }, // black gray
  headerPrimary: {
    color: "#821c1c",
    fontWeight: 900,
    fontSize: "1rem",
  },

  headerSecondary: {
    color: "rgba(243, 167, 18, 1)",
    fontWeight: 900,
    fontSize: "1rem",
  },
  subHeaderSecondary: {
    color: "#e4e4e2",
    fontWeight: 600,
    fontSize: "0.875rem",
  },
}));

export default function WhatIDoCard() {
  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardHeader
        title="What I do?"
        classes={{
          title: classes.headerTitle,
          subheader: classes.headerSubHeader,
          root: classes.headerRoot,
        }}
      />
      <CardContent classes={{ root: classes.cardContentRoot }}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <List className={classes.root}>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <CodeIcon className={classes.textSecondary} />
                </ListItemAvatar>
                <ListItemText
                  primary="Web Development"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        Mainly Backend with some Front-end skills
                      </Typography>
                      {
                        " — Building API based applications and server-side logic as well as the development and maintenance of the central database, ensuring high responsiveness and performance to requests from the frontend"
                      }
                    </React.Fragment>
                  }
                />
              </ListItem>

              <Divider variant="inset" component="li" />

              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  {/* <Avatar alt="Web Design" src={ResumeIcon} /> */}

                  <ColorLensIcon className={classes.textSecondary} />
                </ListItemAvatar>
                <ListItemText
                  primary="Web Design"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        Material design is my favorite
                      </Typography>
                      {
                        " —  Before Jumping into coding my main passion was web design and producing interactive websites, now I'm trying to focus more on the accessabilty, fast page speed and goal driven web design"
                      }
                    </React.Fragment>
                  }
                />
              </ListItem>

              <Divider variant="inset" component="li" />

              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <SettingsInputCompositeIcon
                    className={classes.textSecondary}
                  />
                </ListItemAvatar>
                <ListItemText
                  primary="Devops and Containerization"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        Docker fan!
                      </Typography>
                      {
                        " — Using docker and fast deployment and containerization tools like Forge,easyengine,Netlify,Heroku,PM2 and kubernetes i can speed up the development process."
                      }
                    </React.Fragment>
                  }
                />
              </ListItem>
            </List>
          </Grid>

          <Grid item xs={12} md={6}>
            <List className={classes.root}>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <DnsIcon className={classes.textSecondary} />
                </ListItemAvatar>
                <ListItemText
                  primary="Server Management"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        AWS oriented
                      </Typography>
                      {
                        " — Tracking and monitoring server activities using different tools, Also setting up servers for new projects either on AWS EC2 instance or Digital ocean droplet, Installing all necessary environment tools and packages."
                      }
                    </React.Fragment>
                  }
                />
              </ListItem>

              <Divider variant="inset" component="li" />

              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <CloudIcon className={classes.textSecondary} />
                </ListItemAvatar>
                <ListItemText
                  primary="Cloud services"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        and serverless development
                      </Typography>
                      {
                        " — That would be my favorite game for the next year, using serverless functions and AWS lambda functions would save humanity a lot of time of extra coding and server setting up!"
                      }
                    </React.Fragment>
                  }
                />
              </ListItem>

              {/* <Divider variant="inset" component="li" />

              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <PaymentIcon className={classes.textSecondary} />
                </ListItemAvatar>
                <ListItemText
                  primary="Ecommerce - SPAs"
                  classes={{
                    primary: classes.headerSecondary,
                    secondary: classes.textSecondary,
                  }}
                  secondary={
                    <React.Fragment>
                      <Typography
                        component="span"
                        variant="body2"
                        className={clsx(
                          classes.inline,
                          classes.subHeaderSecondary
                        )}
                      >
                        Yes I do some WordPress and I'm not proud
                      </Typography>
                      {
                        " — Familiar with CMSs like WordPress and dropal for building to go projects and websites"
                      }
                    </React.Fragment>
                  }
                />
              </ListItem> */}
            </List>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}
