import React, { useEffect } from "react";
import { TweenMax, TimelineLite } from "gsap";
import { makeStyles } from "@material-ui/styles";
import heroimg from "../assets/img/vectors/hero.svg";
import tagsvg from "../assets/img/vectors/yellow/11-tag.svg";
import responsivesvg from "../assets/img/vectors/yellow/19-responsive.svg";
import serversvg from "../assets/img/vectors/yellow/18-server.svg";
import {
  Grid,
  Card,
  CardContent,
  Divider,
  Typography
} from "@material-ui/core";
import SkillCard from "./../components/SkillCard";

const useStyles = makeStyles(theme => ({
  blogWrapper: {
    display: "grid",
    gridTemplateRows: "auto 1fr",
    // gridTemplateColumns: "repeat(3,200px)", //example on repeat, that do three columns with the same size
    // gridAutoRows:"15px" // exmple on repeating rows
    gridRowGap: "0px",
    gridTemplateAreas: `
    'banner'
    'allskills'
    'managetools'
    `
  },
  banner: {
    gridArea: "banner"
  },
  skillsContainer: {
    gridArea: "allskills"
  },
  managetools: {
    gridArea: "managetools"
  },
  card: {
    width: "100%"
  },
  cardContentRoot: {
    backgroundColor: "rgba(0, 0, 0, 0.8)"
  }
}));

export default function ResumeSkills() {
  const classes = useStyles();
  useEffect(() => {
    var timeline = new TimelineLite();
    timeline.from("#skillsContainer", 0.8, {
      y: 500,
      ease: "circ.out"
    });
    timeline.from("#banner", 0.5, {
      x: -1500,
      ease: "circ.out"
    });
  });
  return (
    <div className={classes.blogWrapper}>
      <div className={classes.banner} id="banner">
        <Grid container justify="center">
          <img
            src={heroimg}
            style={{ width: "80%", height: "auto" }}
            alt="why are you looking at my alt"
          />
        </Grid>
      </div>
      <div className={classes.skillsContainer} id="skillsContainer">
        <Grid container justify="center">
          <Grid item xs={10} md={12}>
            <Card className={classes.card}>
              <CardContent classes={{ root: classes.cardContentRoot }}>
                <Grid container justify="center">
                  {/* <h4
                    style={{
                      color: "#e4e4e2",
                      width: "80%",
                      textAlign: "center"
                    }}
                  > */}
                  <Typography
                    variant="h6"
                    color="textSecondary"
                    style={{
                      color: "#e4e4e2",
                      width: "80%",
                      textAlign: "center"
                    }}
                  >
                    My First stack ever ( 5 years ago ) was Laravel for the
                    backend and laravel blades for the front-end, Now my fav
                    stack is NodeJs(express) | AWS Lambda for the backend and
                    React for the front-end.
                  </Typography>

                  {/* </h4> */}
                </Grid>

                <Grid container spacing={0} justify="center">
                  {/* <Grid container > */}
                  <Grid item xs={12} md={4}>
                    <SkillCard
                      title="BACKEND SKILLS"
                      subtitle="Languages I speak"
                      subtitle2="Frameworks I love"
                      subtitle3="Databases & ORMs"
                      subtitle4="Dev Tools"
                      subtitle5="Patterns and Techniques"
                      firstlist={["PHP7", "JAVASCRIPT (NodeJs)"]}
                      secondlist={[
                        "Laravel 5.8",
                        "Express-JS",
                        "Slim framework"
                      ]}
                      thirdlist={[
                        "MYSQL with Eloquent",
                        "MYSQL with Sequelize",
                        "MongoDB with Mongoose",
                        "MongoDB with GraphQL",
                        "AWS RDS, Auora"
                      ]}
                      fourthlist={[
                        "Postman",
                        "XAMPP",
                        "NginX",
                        "Apache",
                        "Linux Commands",
                        "Zapier",
                        "Guzzle",
                        "AWS Lambda",
                        "npm"
                      ]}
                      fourthlist2={[
                        "JWT",
                        "Serverless",
                        "phpMyAdmin",
                        "PM2",
                        "Ubunto",
                        "Atom",
                        "VSCode",
                        "googleapis",
                        "Composer"
                      ]}
                      fifthlist={[
                        "Object Oriented Programming",
                        "MVC",
                        "Solid principles",
                        "Restful Apis - ( CRUD operations)"
                      ]}
                      logo={tagsvg}
                    />
                  </Grid>
                  {/* </Grid> */}

                  {/* <Grid container justify="center"> */}
                  <Grid item xs={12} md={4}>
                    <SkillCard
                      title="FRONTEND SKILLS"
                      variant="dark"
                      subtitle="Favourite Framework"
                      subtitle2="Scripting and styling"
                      subtitle4="Tools"
                      firstlist={[" ReactJS"]}
                      secondlist={[
                        "CSS3",
                        "HTML5",
                        "Bootstrap4",
                        "Material UI",
                        "Flexbox",
                        "Redux - state management",
                        "CSS Grid System",
                        "GSAP",
                        "JQuery ( Don't hire me if you are still using it!)"
                      ]}
                      thirdlist={[]}
                      fourthlist={["Chrome Dev Tools", "Codepn", "npm"]}
                      fourthlist2={["Wappalyzer", "Lighthouse", "Google Fonts"]}
                      fifthlist={[]}
                      logo={responsivesvg}
                    />
                  </Grid>
                  {/* </Grid>
                <Grid container justify="center"> */}
                  <Grid item xs={12} md={4}>
                    <SkillCard
                      title="DEVOPS & Analytics"
                      subtitle="Version Control"
                      subtitle2="Servers"
                      subtitle3="Deployment & Containerization"
                      subtitle4="SEO and Analytics"
                      firstlist={["GIT", "Github", "Bitbucket"]}
                      secondlist={["AWS EC2", "Digital Ocean"]}
                      thirdlist={[
                        "Netlify",
                        "Heroku",
                        "Docker",
                        "easyengine",
                        "Forge",
                        "AWS S3",
                        "Digital ocean spaces"
                      ]}
                      fourthlist={[
                        "Google Analytics Reporting Core API",
                        "Facebook Pixels API"
                      ]}
                      fourthlist2={[
                        "Google Tag Manager - GTM ",
                        "Google Business Console"
                      ]}
                      logo={serversvg}
                    />
                  </Grid>
                  {/* </Grid> */}
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </div>
      <div className={classes.managetools} id="managetools"></div>
    </div>
  );
}
